import React from 'react';
import data from '../Data';
import Product from '../Components/Product';

export default function ShopScreen() {
    return (
        <div className="row center">   
        {data.products.map((product)=> { 
            return (
                <Product 
                    key={product._id} 
                    product={product}>
                </Product>
            )})
        }
        </div>
    )
}
