import React from 'react';
import Rating from '../Components/Rating'
import Data from '../Data'
import { Link } from 'react-router-dom'

export default function ProductScreen(props) {
    const product = Data.products.find((x) => x._id === props.match.params.id);
    const { _id, name, image, price, rating, numReviews, description, attribute1, category, countInStock } = product;   
    if(!product) {
        return (
            <div>Product Not Found</div>
        )
    }
    return(
        <div key={_id}>
            <Link to="/">Więcej z kategorii : {category}</Link>
            <div className="row top center">

                <div className="col-2">            
                    <div className="">
                        <img className="large" src={image} alt={name}/>
                    </div>
                </div>

                <div className="col-1">
                    <ul>
                        <li><h1>{name}</h1></li>
                        <li><h1 className="kategoria">{price} zł</h1></li>
                        <li>
                            <Rating 
                                rating={rating} 
                                numReviews={numReviews}
                            ></Rating>
                        </li>
                        <li><span className="kategoria">{description}</span></li>
                        <li><span className="kategoria">{attribute1}</span></li>
                        <li>
                            <div>
                                {countInStock>0 ? (
                                    <span className="success">Produkt dostępny</span>
                                ) : (
                                    <span className="error">Produkt wyprzedany</span>
                                )
                                }
                            </div>
                        </li>
                        <li><button className="primary block">Dodaj do Koszyka</button></li>
                        <li><span className="kategoria">Kategoria: {category}</span></li>
                    </ul>
                </div>
                
            </div>
        </div>
    )
}